body {
    font-family: 'Arial', sans-serif;
    background-color: #f0f0f0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
}

.container {
    text-align: center;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 700px;
    transition: transform 0.3s ease;
}

.container:hover {
    transform: scale(1.02);
}

h1 {
    font-size: 24px;
    color: #333;
}

.instructions {
    margin-bottom: 20px;
}

.pipeline {
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
}

.dropzone {
    width: 90px;
    height: 90px;
    border: 2px dashed #ccc;
    border-radius: 8px;
    background-color: #fafafa;
    transition: background-color 0.3s ease, border-color 0.3s ease;
}

.dropzone:hover {
    background-color: #e0e0e0;
}

.stages {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.stage {
    width: 90px;
    height: 90px;
    background-color: #4caf50;
    color: #fff;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    margin: 10px;
    cursor: grab;
    transition: transform 0.2s ease;
}

.stage:active {
    cursor: grabbing;
    transform: scale(1.1);
}

.check-button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #2196f3;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.check-button:hover {
    background-color: #1976d2;
}

.result {
    margin-top: 20px;
    font-size: 18px;
    color: #333;
}

.hide {
    display: none;
}

.hovered {
    background-color: #e0f7fa;
    border-color: #00acc1;
}
